// ** React Imports
import { Fragment } from 'react'

// ** Custom Components
import NavbarUser from './NavbarUser'
import NavbarBookmarks from './NavbarBookmarks'
import { useLocation } from 'react-router-dom'

const ThemeNavbar = props => {
  // ** Props
  const location = useLocation()
  const { skin, setSkin, setMenuVisibility } = props
  return (
    <Fragment>
      <div className='bookmark-wrapper d-flex align-items-center'>
        <NavbarBookmarks setMenuVisibility={setMenuVisibility} />
        <div className='title'>
          {location.pathname.includes('purchase-list')
            ? 'N HNG'
            : location.pathname.includes('customer')
            ? 'DANH SCH KHCH HNG'
            : location.pathname.includes('lottery')
            ? 'DANH SCH V'
            : location.pathname.includes('history')
            ? 'DANH SCH LCH S'
            : location.pathname.includes('notification')
            ? 'THNG BO'
            : location.pathname.includes('payment-method')
            ? 'PHNG THC THANH TON'
            : location.pathname.includes('system-configuration')
            ? 'CU HNH H THNG'
            : location.pathname.includes('permission')
            ? 'PHN QUYN'
            : location.pathname.includes('account-admin')
            ? 'QUN L NHN VIN'
            : 'DASHBOARD'}
        </div>
      </div>
      <NavbarUser skin={skin} setSkin={setSkin} />
    </Fragment>
  )
}

export default ThemeNavbar
