// @ts-nocheck
import axios from "axios";
import { HOST } from "./../constants/url";
import { toast } from "react-toastify";
import { getQueryString } from "../helper/common";

function send({
  method = "get",
  path,
  data = null,
  query = null,
  headers = {},
  newUrl,
}) {
  return new Promise((resolve) => {
    let url = HOST + `${path}${getQueryString(query)}`;
    if (newUrl) {
      url = `${newUrl}${getQueryString(query)}`;
    }
    let token = window.localStorage.getItem("accessToken");

    if (token) {
      const newToken = token.replace(/"/g, "");
      headers.Authorization = `Bearer ${newToken}`;
    }
    axios({
      method,
      url,
      data,
      headers,
    })
      .then((result) => {
        const data = result.data;
        return resolve(data);
      })
      .catch((error) => {
        const { response = {} } = error;

        const result = response.data ? response.data : null;
        if (!result) {
          toast.warn(
            "Pht sinh li h thng. Lin h tng i  bit thm chi tit."
          );
        } else {
          const { statusCode, message: data, error } = result;
          if (statusCode === 505) {
            toast.warn("Khng c quyn truy cp");
            return resolve(result);
          } else if (
            statusCode === 401 &&
            data === "Expired token received for JSON Web Token validation"
          ) {
            toast.warn("Ht hn truy cp");
            logout();
          } else if (
            statusCode === 400 &&
            data ===
              'child "password" fails because ["password" length must be at least 6 characters long]'
          ) {
            toast.warn("Mt khu phi trn 6 k t");
            return resolve(result);
          } else if (
            (statusCode === 401 && data === "Unauthorized") ||
            (statusCode === 403 && data === "InvalidToken")
          ) {
            toast.warn("Khng c quyn truy cp");
            logout();
          } else if (statusCode === 500) {
            if (error === "duplicate username") {
              toast.warn("Ti khon hoc email  tn ti");
            } else if (error === "failed to update user") {
              toast.warn("Vui lng xem li thng tin");
            } else if (error === "failed to login staff") {
              toast.warn("Sai tn ngi dng hoc mt khu, vui lng nhp li");
            } else if (error === "can not find user") {
              toast.warn("Khng tm thy ngi dng");
            } else if (error === "DUPLICATED_USER_PHONE") {
              toast.warn("S in thoi  tn ti");
            } else if (error === "DUPLICATED_USER_EMAIL") {
              toast.warn("Email  tn ti");
            } else if (error === "DUPLICATED_USER") {
              toast.warn("S in thoi hoc email  tn ti");
            } else if (data === "account is locked") {
              toast.warn("Ti khon  b kha");
            } else if (data === "email is already exist") {
              toast.warn("Email  tn ti");
            } else if (data === "start date and end date is so far") {
              toast.warn("Chn khong thi gian khng qu 12 thng");
            } else if (data === "phone number is already exist") {
              toast.warn("S in thoi  tn ti");
            } else if (error === "can not insert staff") {
              toast.warn("admin khng to c ti khon");
            } else if (data === "username or password is wrong") {
              toast.warn(
                "Ti khon hoc mt khu khng ng, vui lng nhp li"
              );
            } else {
              toast.warn(
                "Pht sinh li h thng. Lin h tng i  bit thm chi tit."
              );
            }
            return resolve(result);
          } else if (statusCode) {
            toast.warn(
              data ||
                "Pht sinh li h thng. Lin h tng i  bit thm chi tit."
            );
            return resolve(result);
          } else {
            return resolve(result.data);
          }
        }
      });
  });
}

function logout() {
  setTimeout(() => {
    localStorage.removeItem("userData");
    localStorage.removeItem("accessToken");
    localStorage.removeItem("refreshToken");

    window.localStorage.clear();
    window.location.href = "/";
  }, 1000);
}

export default {
  send,
};
